<template>
  <!-- 轮播图 -->
  <div class="slideShow">
    <transition-group tag="ul" :name="direction">
      <li v-for="(item,index) in imgs" :key="item.id" v-show="currentIndex == index" @click="start">
        <router-link :to="`/playlist/${item.id}`">
          <div>
            <img :src="item.coverImgUrl" />
            <span>{{item.playCount | formatCount}}万</span>
            <p>{{item.name}}</p>
          </div>
        </router-link>
      </li>
    </transition-group>
    <ol>
      <li
        v-for="i in 10"
        :style="{backgroundColor:currentIndex == i-1 ? activecolor : normalcolor}"
        @click="change(i)"
        :key="i"
      ></li>
    </ol>
    <div class="left" @click="move1">&lt;</div>
    <div class="right" @click="move2">&gt;</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      imgs: [],
      direction: "level",
      normalcolor: "rgba(255, 255, 255, 0.7)",
      activecolor: "red",
      currentIndex: 0,
      timer:null,
      isClear:false
    };
  },
  methods: {
    move1() {
      if (this.direction == "level") {
        this.direction = "level-right";
      }
      this.currentIndex--;
      this.currentIndex < 0 ? (this.currentIndex = this.imgs.length - 1) : this.currentIndex;
      clearInterval(this.timer)
      this.isClear = true
    },
    move2() {
      if (this.direction == "level-right") {
        this.direction = "level";
      }
      this.currentIndex++;
      this.currentIndex > this.imgs.length - 1 ? (this.currentIndex = 0) : this.currentIndex;
      clearInterval(this.timer)
      this.isClear = true
    },
    change(index) {
      if (this.currentIndex > index - 1) {
        if (this.direction == "level") {
          this.direction = "level-right";
        }
      } else {
        if (this.direction == "level-right") {
          this.direction = "level";
        }
      }
      this.currentIndex = index - 1;
      clearInterval(this.timer)
      this.isClear = true
    },
    start(){
      if(this.isClear == true){
        this.timer = setInterval(() => {
          this.currentIndex ++
          this.currentIndex > this.imgs.length - 1 ? (this.currentIndex = 0) : this.currentIndex;
        },1500)
      }
    }
  },
  created() {
    this.$axios.get("/top/playlist/highquality/tags?limit=10").then(res => {
      this.imgs = res.data.playlists
    })
  },
  mounted() {
    this.timer = setInterval(() => {
      this.currentIndex ++
      this.currentIndex > this.imgs.length - 1 ? (this.currentIndex = 0) : this.currentIndex;
    },1500)
  },
  filters: {
    formatCount(val) {
      return (val / 10000).toFixed(2);
    },
  },
};
</script>

<style lang="less" scoped>
.slideShow {
  width: 100%;
  height: 10rem;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
  ul,
  ol {
    list-style: none;
  }
  ul {
    li {
      width: 100%;
      height: 10rem;
      position: absolute;
      left: 0;
      top: 0;
      /* transition: all 2s; */
      a{
        div {
          position: relative;
          span {
            position: absolute;
            top: .5rem;
            right: .5rem;
            background-image: url(data:image/svg+xml;base64,);
            background-repeat: no-repeat;
            background-position-y: .15rem;
            background-size: .6rem;
            color: #fff;
            text-shadow: 0px 0px 2px #000;
            padding-left: .7rem;
            font-size: .6rem;
          }
          p {
            position: absolute;
            left: 50%;
            top: 5rem;
            transform: translateX(-50%) translateY(-50%);
            text-decoration: none;
            font-size: .8rem;
            color: #ddd;
          }
          img{
            width: 100%;
            height: 15rem;
          }
        }
      }
    }
  }
  ol {
    position: absolute;
    left: 50%;
    bottom: .5rem;
    transform: translateX(-50%);
    height: 1.5rem;
    width: 10.5rem;
    &::after{
      content: "";
      display: block;
      clear: both;
    }
    li {
      width: .75rem;
      height: .75rem;
      border-radius: 50%;
      /* background-color: rgba(255, 255, 255, 0.7); */
      float: left;
      cursor: pointer;
      margin: .15rem;
    }
  }
  .left,.right {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 1.5rem;
    line-height: 1.5rem;
    text-align: center;
    width: 1rem;
    background-color: rgba(255, 255, 255, 0.4);
    font-size: .8rem;
    cursor: pointer;
  }
  .left {
    left: 0;
    border-radius: 0 .5rem .5rem 0;
  }

  .right {
    right: 0;
    border-radius: .5rem 0 0 .5rem;
  }

  /* 左右移动 左到右*/
  .level-enter {
    transform: translateX(-100%);
  }

  .level-enter-to,
  .level-leave {
    transform: translateX(0);
  }

  .level-enter-active,
  .level-leave-active {
    transition: all 1s;
  }

  .level-leave-to {
    transform: translateX(100%);
  }

  /* 左右移动 右到左*/
  .level-right-enter {
    transform: translateX(100%);
  }

  .level-right-enter-to,
  .level-leave {
    transform: translateX(0);
  }

  .level-right-enter-active,
  .level-right-leave-active {
    transition: all 1s;
  }

  .level-right-leave-to {
    transform: translateX(-100%);
  }
}


</style>
